@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.modal{
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-170px);
    width: 350px;
    height: 400px;
    margin: 0 auto;
    padding: 40px 20px;
    text-align: center;
}
.imgDiv{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 400px;
    height: 370px;
    transform: translateY(-110px);
}
.imgDiv img{
    width: 350px;
    height: 355px;
    transform: translateY(-5px) translateX(-2px);
}
@media screen and (max-width:520px){
    .imgDiv{
        width: 100%;
        height: 100%;
    }
    .imgDiv img{
        transform: translateY(-15px) translateX(-2px);
    }
   
}
.game-wrapper{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
transform: translateY(190px);
margin: 0 auto;
}
.btn-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
    margin-bottom: 10px;
}
.btn{
  width: 180px;
  outline: none;
  background: transparent;
  border: 1px solid rgba(0,0,0,0.8);
  padding: 8px;
  border-radius: 7px;
  color: rgba(0,0,0,0.8);
  cursor: pointer;
  font-weight: 700;
  margin-top:10px;
  margin-left:10px;
  margin-right:10px;
  font-size: 16px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.btn:hover{
    background-color: rgb(52, 134, 100);
    color: white;
  }
  .slider{
      margin-bottom: 20px;
  }
  @media screen and (max-width:520px){
    .btn{
      width: 100px;
      font-size: 12px;
      padding: 4px;
    }
    .btn-wrapper{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
  }
  .game-wrapper{
    transform: translateY(80px);
    }
  }
.stopwatch-card {
  position: absolute;
  text-align: center;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 40%;
  height: 130px;
  top: 110px;
  left: 50%;
  transform: translate(-50%, -50%);
}

p {
  transform: translateY(15px);
  font-size: 28px;
  font-weight: 700;
}


.btn1 {
  width: 100%;
  outline: none;
  background: transparent;
  border: 1px solid rgba(0,0,0,0.8);
  padding: 5px 10px;
  border-radius: 7px;
  color: rgba(0,0,0,0.8);
  cursor: pointer;
  font-weight: 700;
  margin: 5px;
}
.btn1:hover{
  background-color: rgb(52, 52, 134);
  color: white;
}

.buttons {
  display: flex;
  justify-content: space-evenly;
  width: 150px;
  margin: 0 auto;
  margin-top: 5px;
}

@media screen and (max-width:520px){
  .stopwatch-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 85%;
    height: 70px;
    top: 40px;
  }
  p {
    transform: translateY(10px);
    font-size: 20px;
    font-weight: 700;
  }
  .btn1{
      font-size: 14px;
      transform: translateY(-11px);
      padding: 2px;
  }
}
footer{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 50px;
    position: fixed;
    left: 0;
    bottom: 0;
    background-color: rgb(162, 163, 168);
    color: white;
}
footer p{
    font-size: 13px;
}
@media screen and (max-width:530px){
    footer{
        width: 100%;
        height: 50px;
        position: fixed;
        left: 0;
        bottom: 0;
        background-color: rgb(162, 163, 168);
        color: white;
    }
}
 a{
    color: rgb(245, 248, 245);
    background-color: rgb(255, 47, 144);
    border-radius: 50%;
    text-decoration: none;
    list-style: none;
 }
footer span{
    margin-left: 10px;
    margin-right: 10px;
    font-size: 25px;
    color: rgb(245, 248, 245);
    background-color: rgb(255, 47, 144);
    border-radius: 50%;
    transform: translateY(-10px);
    cursor: pointer;
    animation: fly 0.8s linear infinite;
}
@keyframes fly{
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-10px);
    }
    100%{
        transform: translateY(0px);
    }
}
